import vuePayKeyboard from './vue-pay-keyboard.vue' // 
const paykeyboard = {
    install(Vue, options) {
        Vue.component(vuePayKeyboard.name, vuePayKeyboard) // vuePayKeyboard.name name
        //  this.$xxx  
        // Vue.prototype.$xxx  //  this.$xxx 
        // $  
    }
}
if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(paykeyboard);
}
export default paykeyboard // ..


